/*-------- TYPED TEXT ---------------*/
.typed-text {
    font-size: 1.5rem;
    color: #ffffff;
    color: var(--primary-white);
    margin: 2rem 2rem 1rem 2rem;
    font-family: 'Raleway', sans-serif !important;
    border: 3px groove rgb(220, 20, 60);
    border: 3px groove var(--primary-red);
    border-radius: 5px;
    padding: 0.625rem 0.75rem 0.75rem 0.75rem;
    text-transform: uppercase;
    background-color: rgba(0, 0, 58, 0.1);

  }

#contact{
    background-color: rgba(0, 0, 58, 0.1);
  }

#contact:hover {
    text-decoration: none;
    background-color: rgb(220, 20, 60);
    background-color: var(--primary-red);
    color: #ffffff;
    color: var(--primary-white);
    transition:  .2s ease-in-out;
    cursor: pointer;
  }

.btn-main-offer {
    font-size: 20px;
    letter-spacing: 2px;
    border: 3px solid rgb(220, 20, 60);
    border: 3px solid var(--primary-red);
    border-radius: 5px;
    text-transform: uppercase;
    padding: 0.625rem 0.75rem 0.75rem 0.75rem;
    color: #ffffff;
    color: var(--primary-white);
    margin: 1rem 2rem 2rem 2rem;
  }

  .btn-main-offer:hover {
    text-decoration: none;
    background-color: rgb(220, 20, 60);
    background-color: var(--primary-red);
    color: #ffffff;
    color: var(--primary-white);
    transition:  .2s ease-in-out;
    cursor: pointer;
  }
  
@media(max-width: 768px) {
  .btn-main-offer { 
    font-size: 15px;
    font-weight: 500;
    letter-spacing: 2px;
    border: 2px solid rgb(220, 20, 60);
    border: 2px solid var(--primary-red);
  }
  .main-info h1 {
    letter-spacing: 0.2rem;
    font-size : 20px;
    font-weight: 600;
  }
  .typed-text {
    font-size: 20px;
    text-transform: uppercase;
  }
}
