    text-transform: uppercase;
  }
  
  .services .circle {
    position: relative;
    margin: 0.375rem auto;
    background: rgb(250, 171, 0);
    background: var(--primary-orange);
    border-radius: 50%;
    width: 3.125rem;
    height: 3.125rem;
  }
  
  .services .icon {
    font-size: 1.4rem;
    color: #ffffff;
    color: var(--primary-white);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .services .box {
    height: 21rem;
    border-bottom: 0.3125rem solid rgb(250, 171, 0);
    border-bottom: 0.3125rem solid var(--primary-orange);
    padding: 0.625rem;
    margin-bottom: 1.875rem;
